export default {
  state: {
    serviceName: '',
    instenceName: '',
    serviceList: [],
    instenceList: []
  },
//   getters: {
//     activeSetting (state) {
//       return state.serviceName
//     }
//   },
  action: {
    commitServiceList() {
       this.$store.dispatch('createUser')
    }
  },
  mutations: {
    setService(state, serviceName) {
      state.serviceName = serviceName
    },
    setInstence(state, instenceName) {
      state.instenceName = instenceName
    },
    setService(state, serviceList) {
      state.serviceList = serviceList
    },
    setInstence(state, instenceList) {
      state.instenceList = instenceList
    }
  }
}
